@import "../../../style/theme/default/var.css";
body {font-family: '微软雅黑'; font-size: 13px; line-height: 1.42857143; color: var(--theme-font-color-primary); background-color: var(--color-white); }
table {border-collapse: collapse; border-spacing: 0; }

.margin-5-auto{margin:5px auto;}
.margin-top-10{ margin-top: 10px;}
.produce_title{ color:var(--color-white); background:var(--theme-background-color-secondary);}
.text-center{text-align:center;}

.produce_price{display:inline;}
.produce_info{border:1px solid var(--theme-border-color-primary);}
.itemPic{}
/* 推荐 */
.recommend{height: 25px; border-collapse: collapse;}
.recommend_list .addpic{border: 1px solid var(--theme-border-color-primary); box-sizing: border-box; margin: 8px auto; width: 90%; height: 160px; line-height: 160px; text-align: center; font-size: 20px; font-weight: bold;}
.recommend_list .addinfo{ margin: 10px auto; width: 90%; height: 90px; text-align: left;}
.recommend_list .addname{ margin:5px 0; height: 36px; @utils-ellipsis 2; width:100%;}
.recommend_list .addprice{height: 30px; margin-top: 0; margin-bottom: 0; }
.recommend_list .addprice td{padding: 5px 10px;}
/*预览*/
.preview{margin:5px auto; width:680px; }
.preview img{width:100%;}
.preview_bor{ padding: 5px 10px 5px 0px; margin: 10px 0px;}
.preview_bor div:first-child{font-weight:700;   line-height: 40px;font-size: 18px;}
.preview_bor div:nth-child(2){  text-indent: 30px;}
.preview .content{text-indent:32px; }
.preview_btn{border: 1px solid transparent;display: inline-block;font-weight: normal;text-align: center;vertical-align: middle;padding: 6px 12px; margin: 5px;font-size: 13px;line-height: 1.42857143;color: var(--color-white); background-color: var(--theme-color-primary); border-color: var(--theme-color-primary); }
.preview .recommend_list{ text-align: center;margin-top: 10px; border-collapse: collapse; border-spacing: 0;}
.preview .recommend_list td {border: 1px solid var(--theme-border-color-primary);padding:0px;width: 25%; }
.preview .recommend_price{ text-align: center;margin-top: 10px; border-collapse: collapse; border-spacing: 0;}
.preview .recommend_price td{width: 50%;}
/* 物流 订单信息 */
.preview .wuliu{background-color: transparent;border-collapse: collapse;border-spacing: 0;}
.preview .wuliu td{border:1px solid var(--theme-border-color-primary); border-collapse: collapse; border-spacing: 0; padding:5px 10px;}
.preview .wuliu .border-bottom-0{border-bottom: none;}
/* 自定义 */
.user_defined img{width:100%;}
.hide{
  display:none;
}



